          <!-- ÌõÑÍ∏∞ Í∏Ä ÏÑπÏÖò -->
          <div v-else>
            <!-- ÌòÑÏû¨ ÏÇ¨Ïö©ÏûêÍ∞Ä Íµ¨Îß§ÏûêÏùº Îïå -->
            <div v-if="product.B_uid === userID">
              <h3>ÌõÑÍ∏∞ Í∏Ä</h3>
              <textarea
