              </div>
              <div v-else>
                <p><strong>ÎÇ¥Ïö©:</strong> {{ product.ReviewContent }}</p>
                <div class="ratings-display">
                  <p><strong>Î≥ÑÏ†ê:</strong></p>
                  <!-- Î≥ÑÏ†ê Ï∂úÎ†• -->
                  <span
                    v-for="star in product.Ratings"
                    :key="'display-' + star"
                    class="star_static"
                  >
                    ‚òÖ
                  </span>
                </div>
                <p><strong>Îì±Î°ùÏùº:</strong> {{ formatDate(product.ReviewReg_date) }}</p>
              </div>
              <button
                v-if="!product.ReviewContent"
                @click="submitReview"
                class="review-submit-button"
              >
                ÌõÑÍ∏∞ Ï†ÄÏû•
              </button>
            </div>
            <!-- ÌòÑÏû¨ ÏÇ¨Ïö©ÏûêÍ∞Ä Íµ¨Îß§ÏûêÍ∞Ä ÏïÑÎãê Îïå -->
            <div v-else>
              <div v-if="!product.ReviewContent">
                <h4 align="center">ÌåêÎß§ ÏôÑÎ£å.</h4>
                <h4 align="center">Î¶¨Î∑∞Í∞Ä ÏïÑÏßÅ Îì±Î°ùÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.</h4>
              </div>
              <div v-else>
                <p><strong>ÎÇ¥Ïö©:</strong> {{ product.ReviewContent }}</p>
                <div class="ratings-display">
                  <p><strong>Î≥ÑÏ†ê:</strong></p>
                  <!-- Î≥ÑÏ†ê Ï∂úÎ†• -->
                  <span
                    v-for="star in product.Ratings"
                    :key="'display-' + star"
                    class="star_static"
                  >
                    ‚òÖ
                  </span>
                </div>
                <p><strong>Îì±Î°ùÏùº:</strong> {{ formatDate(product.ReviewReg_date) }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- ÎÇ¥Ïö© -->
    <section class="product-description">
      <h2>ÏÉÅÌíà ÎÇ¥Ïö©</h2>
      <p>{{ product.Content }}</p>
    </section>
    <section class="qna-section">
      <h2>ÏßàÎ¨∏ Î∞è ÎãµÎ≥Ä</h2>
      <div
        v-for="(item) in qna"
        :key="item.QuesNo"
        class="qna-item"
        @click="toggleAnswerInput(item.QuesNo)"
      >
        <!-- ÏßàÎ¨∏ -->
        <div v-if="item.Q_private === 1 && !(userID === item.Uid || product.Uid === userID)">
          <span class="lock-icon">üîí</span>   ÎπÑÎ∞Ä ÏßàÎ¨∏ÏûÖÎãàÎã§.
        </div>
        <div v-else>
          <p class="question">
            <strong>ÏûëÏÑ±Ïûê: </strong> {{ item.Username }}
            <span class="q-content"><strong>Q:</strong> {{ item.Question }}</span>
            <span class="q-date">({{ formatDate(item.Q_date) }})</span>
            <!-- ÏÇ≠Ï†ú Î≤ÑÌäº -->
            <button
              v-if="userID === item.Uid"
              @click="deleteQnA(item.QuesNo)"
              class="qna-delete-button"
            >
              ÏÇ≠Ï†ú
            </button>
          </p>
        </div>
        <!-- ÎãµÎ≥Ä -->
        <div v-if="item.A_private === 1 && !(userID === item.Uid || product.Uid === userID)">
          „Ñ¥ <span class="lock-icon">üîí</span>   ÎπÑÎ∞Ä ÎãµÎ≥ÄÏûÖÎãàÎã§.
        </div>
        <div v-else>
          <p v-if="item.AnsNo" class="answer">
            <strong>„Ñ¥ A:</strong> {{ item.Answer }}
            <span class="a-date">({{ formatDate(item.A_date) }})</span>
            <button
              v-if="userID === product.Uid"
              @click="deleteQnA(item.AnsNo)"
              class="qna-delete-button"
            >
              ÏÇ≠Ï†ú
            </button>
          </p>
          <!-- ÎãµÎ≥Ä ÏûÖÎ†• ÏÑπÏÖò -->
          <div v-else-if="userID === product.Uid">
            <div
              v-if="selectedQuestionIndex === item.QuesNo"
              class="answer-input"
              @click.stop
            >
              <textarea
                v-model="newQnA"
                placeholder="ÎãµÎ≥ÄÏùÑ ÏûëÏÑ±Ìï¥Ï£ºÏÑ∏Ïöî."
                class="answer-textarea"
              ></textarea>
              <label>
                <input
                  type="radio"
                  value="0"
                  v-model="Private"
                />
                Í≥µÍ∞ú
              </label>
              <label>
                <input
                  type="radio"
                  value="1"
                  v-model="Private"
                />
                ÎπÑÍ≥µÍ∞ú
              </label>
              <button @click.stop="submitQnA()" class="submit-answer-button">
                ÎãµÎ≥Ä Ï†ÄÏû•
              </button>
            </div>
          </div>
        </div>
      </div>

      <!-- ÏßàÎ¨∏ ÏûëÏÑ± ÏÑπÏÖò -->
      <div v-if="userID !== product.Uid" class="add-question">
        <textarea
          v-model="newQnA"
          placeholder="ÏßàÎ¨∏ÏùÑ ÏûëÏÑ±Ìï¥Ï£ºÏÑ∏Ïöî."
          class="question-textarea"
        ></textarea>
        <label>
          <input
            type="radio"
            value="0"
            v-model="Private"
          />
          Í≥µÍ∞ú
        </label>
        <label>
          <input
            type="radio"
            value="1"
            v-model="Private"
          />
          ÎπÑÍ≥µÍ∞ú
        </label>
        <button @click="submitQnA" class="submit-question-button">
          ÏßàÎ¨∏ Îì±Î°ù
        </button>
      </div>
    </section>

    <!-- ÏÇ≠Ï†ú Î≤ÑÌäº -->
    <div class="action-buttons-container">
      <button 
        v-if="product.Uid === userID || auth === 1" 
        @click="deleteProduct" 
        class="delete-button"
      >
        ÏÇ≠Ï†ú
      </button>
      <button 
        v-if="product.Uid === userID" 
        @click="updateProduct" 
        class="update-button"
      >
        ÏàòÏ†ï
      </button>
    </div>
  </div>
  <div v-else>
    <p>Î°úÎî© Ï§ë...</p>
  </div>
</template>

<script>
import axios from "axios";
import { jwtDecode } from "jwt-decode";

export default {
  props: ["ino"],
  data() {
    return {
      product: {},
      qna: {},
      userID: "",
      auth: "",
      reportContent: "",
      imageSources: [],
      currentImageIndex: 0,
      ReviewContent: "", // ÌõÑÍ∏∞ ÎÇ¥Ïö©
      Ratings: 0, // Î≥ÑÏ†ê (1~5 Ï†ïÏàò Í∞í)
      ReviewReg_date: "",
      Private: 0,// ÏßàÎ¨∏, ÎãµÎ≥Ä Í∏ÄÏùò Í≥µÍ∞ú/ÎπÑÍ≥µÍ∞ú Ïó¨Î∂Ä
      selectedQuestionIndex: null, // ÌòÑÏû¨ ÏÑ†ÌÉùÎêú ÏßàÎ¨∏ Ïù∏Îç±Ïä§
      newQnA: "", // ÏÉàÎ°ú ÏûÖÎ†•ÌïòÎäî ÏßàÎ¨∏/ÎãµÎ≥Ä ÎÇ¥Ïö©
    };
  },
  methods: {
    async fetchProductDetails() {
      try {
        const response1 = await axios.get(`http://localhost:3000/read/${this.ino}`);
        if (response1.data) {
          const token = localStorage.getItem("token");
          const user = jwtDecode(token);
          this.userID = user.userID;
          this.auth = user.auth;
          this.product = response1.data[0];

          this.imageSources = [
            this.product.Image,
            this.product.Subimg1,
            this.product.Subimg2,
          ].filter((img) => img);
        } else {
          console.error("ÏÉÅÌíà Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.");
        }
        const response2 = await axios.get(`http://localhost:3000/readQnA/${this.ino}`);
        if (response2.data) {
          this.qna = response2.data;
          console.log(this.qna);
        } else {
          console.error("ÏÉÅÌíà Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.");
        }
      } catch (error) {
        console.error("ÏÉÅÌíà Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏Ïò§Îäî Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
      }
    },
    getCurrentImage() {
      return `data:image/jpeg;base64,${this.imageSources[this.currentImageIndex]}`;
    },
    nextImage() {
      if (this.currentImageIndex < this.imageSources.length - 1) {
        this.currentImageIndex += 1;
      }
    },
    prevImage() {
      if (this.currentImageIndex > 0) {
        this.currentImageIndex -= 1;
      }
    },
    formatDate(dateString) {
      const options = { year: "numeric", month: "2-digit", day: "2-digit",
        hour: "2-digit", minute: "2-digit"};
      return new Date(dateString).toLocaleDateString("ko-KR", options);
    },
    async likeProduct() {
      try {
        if (this.product.Uid !== this.userID) {
          const response = await axios.post(`http://localhost:3000/hit/${this.ino}`, {
            Uid: this.userID,
          });
          if (response.data.success) {
            this.product.Like_cnt += 1;
          } else {
            alert("Ïù¥ÎØ∏ Ï¢ãÏïÑÏöîÎ•º ÎàåÎ†ÄÏäµÎãàÎã§.");
          }
        }
      } catch (error) {
        console.error("Ï∞ú ÏóÖÎç∞Ïù¥Ìä∏ Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
      }
    },
    async reportProduct() {
      try {
        if (this.product.Uid === this.userID)
          return;
        if (this.reportContent.trim() === "") {
          alert("Ïã†Í≥† ÎÇ¥Ïö©ÏùÑ ÏûëÏÑ±Ìï¥Ï£ºÏÑ∏Ïöî.");
          return;
        }
        const response = await axios.post(`http://localhost:3000/report/${this.ino}`, {
          Uid: this.userID,
          content: this.reportContent,
        });
        if (response.data.success) {
          alert("Ïã†Í≥†Í∞Ä Ï†ëÏàòÎêòÏóàÏäµÎãàÎã§.");
          this.product.Rp_cnt += 1;
          this.reportContent = "";
        } else {
          alert("Ïù¥ÎØ∏ Ïã†Í≥†Ìïú Í≤åÏãúÎ¨ºÏûÖÎãàÎã§.");
          this.reportContent = "";
          console.error("Ïã†Í≥† Ï§ëÎ≥µ");
        }
      } catch (error) {
        console.error("Ïã†Í≥† Ï≤òÎ¶¨ Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
      }
    },
    async deleteProduct() {
      try {
        if (!confirm("Ï†ïÎßêÎ°ú ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?"))
          return;
        const response = await axios.get(`http://localhost:3000/delete/${this.ino}`);
        if (response.data.success) {
          alert("Í≤åÏãúÍ∏ÄÏù¥ ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.");
          this.$router.push("/search");
        } else {
          console.error("Í≤åÏãúÍ∏Ä ÏÇ≠Ï†ú Ïã§Ìå®");
        }
      } catch (error) {
        console.error("Í≤åÏãúÍ∏Ä ÏÇ≠Ï†ú Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
      }
    },
    requestProduct() {
      this.$router.push(`/request/${this.ino}`); // Íµ¨Îß§ ÏöîÏ≤≠ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
    },
    updateProduct() {
      this.$router.push(`/update/${this.ino}`);
    },
    setRating(rating) {
      this.Ratings = rating; // ÏÑ†ÌÉùÌïú Î≥ÑÏ†ê ÏÑ§Ï†ï
    },
    async submitReview() {
      if (this.Ratings === 0 || !this.ReviewContent) {
        alert("ÎÇ¥Ïö© Î∞è Î≥ÑÏ†êÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî!");
        return;
      }
      try {
        const response = await axios.post(`http://localhost:3000/review/${this.ino}`, {
          S_uid: this.product.Uid,
          B_uid: this.userID,
          Content: this.ReviewContent,
          Ratings: this.Ratings, // Î≥ÑÏ†êÎèÑ ÏÑúÎ≤ÑÎ°ú Ï†ÑÏÜ°
        });
        if (response.data.success) {
          alert("ÌõÑÍ∏∞Í∞Ä Ï†ÄÏû•ÎêòÏóàÏäµÎãàÎã§.");
          window.location.reload();
        } else {
          alert("ÌõÑÍ∏∞ Ï†ÄÏû•Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
        }
      } catch (error) {
        console.error("ÌõÑÍ∏∞ Ï†ÄÏû• Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
      }
    },
    toggleAnswerInput(QuesNo) {
      if (this.selectedQuestionIndex === QuesNo) {
        this.selectedQuestionIndex = null; // Ïù¥ÎØ∏ Ïó¥Î†§ÏûàÏúºÎ©¥ Îã´Í∏∞
      } else {
        this.selectedQuestionIndex = QuesNo; // ÏÑ†ÌÉùÎêú ÏßàÎ¨∏ Ïó¥Í∏∞
      }
    },
    async deleteQnA(QuesNo) {
      if (!confirm("Ï†ïÎßêÎ°ú ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?")) return;

      try {
        const response = await axios.delete(`http://localhost:3000/deleteQnA/${QuesNo}`);
        if (response.data.success) {
          alert("ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.");
          window.location.reload();
        } else {
          alert("ÏÇ≠Ï†ú Ïã§Ìå®");
          console.error("ÏÇ≠Ï†ú Ïã§Ìå®:", response.data.error);
        }
      } catch (error) {
        alert("ÏÇ≠Ï†ú Ï§ë Ïò§Î•ò Î∞úÏÉù");
        console.error("ÏÇ≠Ï†ú Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
      }
    },
    async submitQnA() {
      if (!this.newQnA.trim()) {
        alert("ÎãµÎ≥Ä ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
        return;
      }

      try {
        const response = await axios.post(`http://localhost:3000/writeQnA/${this.ino}`, {
          Ino: this.ino, // ÏÉÅÌíà Î≤àÌò∏
          Content: this.newQnA,
          Private: this.Private, // ÎπÑÎ∞Ä Ïó¨Î∂Ä (0: Í≥µÍ∞ú, 1: ÎπÑÍ≥µÍ∞ú) - ÏòàÏãúÎ°ú ÏÑ§Ï†ï
          Q_uid: this.userID, // ÏßàÎ¨∏ ÏûëÏÑ±Ïûê ID
          Ans_Qno: this.selectedQuestionIndex,
        });
        if (response.data.success) {
          alert("Îì±Î°ùÎêòÏóàÏäµÎãàÎã§.");
          this.newQnA= "";
          window.location.reload();
        } else {
          alert("Îì±Î°ù Ïã§Ìå®");
          console.error("Îì±Î°ù Ïã§Ìå®:", response.data.error);
        }
      } catch (error) {
        alert("Îì±Î°ù Ï§ë Ïò§Î•ò Î∞úÏÉù");
        console.error("Îì±Î°ù Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
      }
    },
  },
  mounted() {
    this.fetchProductDetails();
  },
};
</script>

<style scoped>
