  text-align: left;
}

.report-textarea {
  width: 100%;
  height: 100px; /* Ïã†Í≥† textarea ÎÜíÏù¥ Ï°∞Ï†ï */
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.report-button {
  background-color: #e74c3c;
  color: white;
